.nav {
    
    display: flex;
    max-width: 700px;
    justify-content: right;
    margin: 20px auto;
    padding: 20px;
    background-color: #22a6b3;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);  
   
    height: 50px;
  }
  
  .nav ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
  }
  
  .nav-button {
    height: 100%;
    background-color: #22a6b3;
    color: white;
    border: none;
    padding: 10px 20px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    margin: 4px 2px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .nav-button:hover {
    background-color: #548181;
    color: #a8c0c0;
    }
  
  .nav-button a {
    font-size: 24px;
    color: white;
    font-weight: bold;
    text-decoration: none;
    display: block;
  }
  
  .nav-button:focus a {
    outline: none;
    text-decoration: none;
  }
  