#container {
  max-width: 700px;
  margin: 0 auto;
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.header {
  font-size: 24px;
  font-weight: bold;
  text-align: center;
  margin-bottom: 20px;
  color: #333;
}

.add-new-book {
  display: flex;
  font-weight: 500;
  flex-direction: column;
  gap: 15px;
}

.add-new-book div {
  display: flex;
  flex-direction: column;
}

.add-new-book label {
  font-size: 16px;
  margin-bottom: 5px;
  color: #555;
  font-weight: bold;
}

.add-new-book input[type="text"] {
  padding: 10px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

#button-container {
  
  display: flex;
  flex-direction: row;
  justify-content: space-between;  
}

.go-back {
  
  justify-self:left;
  padding: 10px 15px;
  font-size: 16px;
  font-weight: bold;
  color: white;
  background-color: #6c757d;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.go-back:hover {
  background-color: #5a6268;
}

.add-new-book input[type="submit"] {
  margin-right: 10px;
  padding: 10px 15px;
  font-size: 16px;
  font-weight: bold;
  color: white;
  background-color: #28a745;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.add-new-book input[type="submit"]:hover {
  background-color: #218838;
}
#textarea{
  height: 100px;
  font-size: 16;
  font-size: 16px;
  margin-bottom: 5px;
  color: #555;
  font-weight: bold;
  padding: 12px 20px;
  background-color: #fff5f5;
  border: 2px solid #ccc;
  border-radius: 4px;
  line-height: 1.5;
  
}